  height: 300px;
  -webkit-transform-style: preserve-3d;
          transform-style: preserve-3d;
  -webkit-perspective: 2000px;
          perspective: 2000px;
  transition: 0.4s;
  text-align: center;
  padding: 3%;
}
.box-animate:before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: transparent;
  border-top: 20px solid #3ad4af;
  border-left: 20px solid#3ad4af;
  box-sizing: border-box;
}
.box-animate:after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  border-bottom: 20px solid #3ad4af;
  border-right: 20px solid #3ad4af;
  box-sizing: border-box;
}

.box-animate .text-animate {
  position: absolute;
  top: 30px;
  left: -30px;
  width: calc(100% + 60px);
  height: calc(100% - 60px);
  background-color: #2c3a47;
  border-radius: 3px;
  transition: 0.4s;
}

