.login-signin form{
    position: static;
    margin-top: 1%;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
}

.login-input-field{
    max-width: 570px;
    width: 100%;
    height: 50px;
    background-color: white;
    margin: 12px 0px;
    border-radius: 10px;
    display: grid;
    grid-template-columns: 100% 85%;
    padding: 0.2rem 0.02rem;    
}

.login-input-field input{
    max-width: 560px;
    width: 100%;
    height: 45px;
    font-weight: 600;
    font-size: 1rem;
    color: #333;
    margin: 0px 5px;
    border-radius: 10px;
}

.login-input-field input::-webkit-input-placeholder{
    color:#aaa;
    font-size: 1rem;
    font-weight: 500;
}

.login-input-field input:-ms-input-placeholder{
    color:#aaa;
    font-size: 1rem;
    font-weight: 500;
}

.login-input-field input::placeholder{
    color:#aaa;
    font-size: 1rem;
    font-weight: 500;
}

.login-button a{
    text-decoration: none;
    color: white;
    font-size: 1.3rem;
}

.login-button{
    width: 200px;
    height: 45px;
    border: none;
    outline: none;
    cursor: pointer;
    background-color: black;
    color: white;
    font-weight: 500;
    margin: 15px 15px;
    font-size: 10px;
    left: 0;  
    display: inline-block;  
    text-decoration: none;
}

.login-button:hover{
    background-image: linear-gradient(120deg, #3e8e41 100%, #8fd3f4 0%);
    color:black;
    font-size: 20px;
    font-weight: 800;
    letter-spacing: 3px;
}

.login-button a:hover{
    background-image: linear-gradient(120deg, #3e8e41 100%, #8fd3f4 0%);
    color:black;
    font-size: 20px;
    font-weight: 800;
    letter-spacing: 3px;
}

.login-button-group button{
    border-radius: 0px;
    font-family: 'jost', sans-serif;
    font-size: 1.3rem;
}

