.contact-page {
  /* background-color: azure; */
  background-color: white;
  padding: 2rem;
  width: 70%;
  margin: 8rem auto;
  border-radius: 12px;
  /* border: 1px solid rgb(141, 136, 136); */
  box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.3);
  display: grid;
  grid-template-columns: 1fr 1fr;
  /* text-align: center; */
  /* height: 60vh; */
  grid-gap: 3rem;
  gap: 3rem;
}
.contact-page__col-1 {
  width: 100%;
}
.contact-page__heading {
  font-size: 2.5rem;
  margin-bottom: 0;
  text-align: center;
  color: transparent;
  background: #373b44; /* fallback for old browsers */ /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(to right, #4286f4, #373b44);
  -webkit-background-clip: text;
  background-clip: text;
}
.contact-page__form {
  padding: 1rem 2rem;
  width: 100%;
}
.contact-page__input {
  height: 2.5rem;
  display: block;
  width: 100%;
  margin-bottom: 0.5rem;
  padding: 0.6rem;
  border-radius: 8px;
  border: none;
  /* transition: all 0.2s; */
  background-color: rgb(241, 234, 234);
  transition: all 0.5s;
}
.contact-page__input::-webkit-input-placeholder {
  padding: 0.3rem;
}
.contact-page__input:-ms-input-placeholder {
  padding: 0.3rem;
}
.contact-page__input::placeholder {
  padding: 0.3rem;
}
.contact-page__input:valid {
  border: none;
}
.contact-page__input:focus {
  outline: none;
  border: 1px solid rgba(19, 19, 61, 0.74);
}
.contact-page__label {
  color: rgb(17, 15, 46);
  text-align: left;
  font-weight: bold;

  display: block;
  padding: 1.2rem 0.5rem;
}
.contact-page__message {
  width: 100%;
  border-radius: 3%;
  border: 1px solid #ccc;
  margin-bottom: 0.8rem;
}
.contact-page__col-2 {
  /* background-image: url("https://image.shutterstock.com/image-illustration/creative-forex-chart-on-blue-260nw-1377926345.jpg"); */
  background-size: cover;
  background-position: cover;
  display: flex;
  /* height: 100%; */
  align-items: center;
}
.contact-page__img {
  width: 100%;
}
.contact-page__button {
  margin-left: 1rem;
  padding: 0.8rem 2rem;
  font-size: 1.2rem;
  font-weight: 400;
  color: white;
  background: linear-gradient(to right, #3ad4af, #426fd8);

  border: none;
  border-radius: 2rem;
  transition: 0.2s;
}
.contact-page__button:hover {
  -webkit-transform: translateY(-0.2rem);
          transform: translateY(-0.2rem);
  background: linear-gradient(to right, #3ad4af, #426fd8);
}
.contact-page__button:active {
  -webkit-transform: translateY(0rem);
          transform: translateY(0rem);
}
@media screen and (max-width: 800px) {
  .contact-page {
    width: 90%;
    grid-template-columns: 100%;
    /* grid-template-rows: 10% 1fr; */
    grid-gap: 1rem;
    gap: 1rem;
    /* margin: 80rem 0; */
    height: auto;
    /* padding: 2rem; */

    /* margin-bottom: 0; */
  }
  .contact-page__col-2 {
    grid-row: 1/2;
  }
  .contact-page__col-1 {
    /* grid-area: 1/1/-1/-1; */
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
  }
  .contact-page__heading {
    font-size: 2.2rem;
  }
  .contact-page__col-2 {
    flex-direction: column;
  }
  .contact-page__img {
    justify-self: center;
    height: 100%;
    width: 50%;
  }
}

