/* Responsive Media Query */
@media (max-width: 1240px) {
  .sidebar_admin {
    width: 160px;
  }
  .sidebar_admin.active {
    width: 220px;
  }
  .home-section {
    width: calc(100% - 60px);
    left: 60px;
  }
  .sidebar_admin.active ~ .home-section {
    overflow: hidden;
    left: 220px;
  }
  .home-section nav {
    width: calc(100% - 160px);
    left: 160px;
  }
  .sidebar_admin.active ~ .home-section nav {
    width: calc(100% - 220px);
    left: 220px;
  }
}
@media (max-width: 1150px) {
  .home-content .sales-boxes {
    flex-direction: column;
  }
  .home-content .sales-boxes .recent-sales {
    width: 100%;
  }
  .details {
    width: 100%;
  }
  .home-content .sales-boxes .box_home {
    width: 100%;
    overflow-x: scroll;
    margin-bottom: 30px;
  }
  .home-content .sales-boxes .top-sales {
    margin: 0;
  }
}
@media (max-width: 1000px) {
  .overview-boxes .box_home {
    width: calc(100% / 2 - 15px);
    /* margin-bottom: 15px; width: 50%; */
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    margin-right: 1%;
    display: block;
  }
}
@media (max-width: 700px) {
  .home-section nav .sidebar_admin-button .dashboard,
  .home-section nav .profile-details .admin_name,
  .home-section nav .profile-details i {
    display: block;
  }
  .home-section nav .profile-details {
    height: 50px;
    min-width: 40px;
  }
  .home-content .sales-boxes .sales-details {
    width: 500px;
  }
  .menu-container .button-a {
    margin-left: 1%;
  }
}
@media (max-width: 550px) {
  .overview-boxes .box_home {
    width: 100%;
    margin-bottom: 15px;
  }
  .sidebar_admin.active ~ .home-section nav .profile-details {
    display: block;
  }
}

